const valid = ({ fullname, username, email, password, cf_password }) => {
  const err = {};

  if (!fullname) {
    err.fullname = 'Vui lng thm tn y  ca bn.';
  } else if (fullname.length > 25) {
    err.fullname = 'Tn y  phi nh hn 25 k t.';
  }

  if (!username) {
    err.username = 'Vui lng thm tn ng nhp ca bn.';
  } else if (username.replace(/ /g, '').length > 25) {
    err.username = 'Tn ng nhp phi nh hn 25 k t.';
  }

  if (!email) {
    err.email = 'Vui lng thm email ca bn.';
  } else if (!validateEmail(email)) {
    err.email = 'nh dng email khng ng.';
  }

  if (!password) {
    err.password = 'Vui lng thm mt khu ca bn.';
  } else if (password.length < 6) {
    err.password = 'Mt khu phi t 6 k t tr ln.';
  }

  if (password !== cf_password) {
    err.cf_password = 'Mt khu khng trng khp.';
  }

  return {
    errMsg: err,
    errLength: Object.keys(err).length,
  };
};

function validateEmail(email) {
  // eslint-disable-next-line
  const re =
    /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
  return re.test(email);
}

export default valid;
